.page-index .header {
  height: 400px; }

.page-index .coin {
  display: inline-block;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: yellow; }

.page-index .fly {
  position: absolute;
  top: 50vh;
  left: 50vw;
  -webkit-animation: fly-coin 1 .5s linear forwards;
          animation: fly-coin 1 .5s linear forwards; }

.page-index .coin0 {
  -webkit-animation-delay: 0s;
          animation-delay: 0s; }

.page-index .coin1 {
  -webkit-animation-delay: .1s;
          animation-delay: .1s; }

.page-index .coin2 {
  -webkit-animation-delay: .2s;
          animation-delay: .2s; }

.page-index .coin3 {
  -webkit-animation-delay: .3s;
          animation-delay: .3s; }

.page-index .coin4 {
  -webkit-animation-delay: .4s;
          animation-delay: .4s; }

.page-index .coin5 {
  -webkit-animation-delay: .5s;
          animation-delay: .5s; }

.page-index main {
  padding: 30px; }

.page-index .xscroll-box {
  display: flex;
  width: 100%;
  overflow-x: scroll; }

.page-index .xscroll-box > li {
  flex-shrink: 0;
  width: 200px;
  height: 100px;
  background-color: yellowgreen;
  margin-right: 10px;
  margin-bottom: 50px; }

@-webkit-keyframes fly-coin {
  from {
    top: 50vh;
    left: 50vw;
    -webkit-transform: scale(1);
            transform: scale(1); }
  to {
    top: 0;
    left: 0;
    -webkit-transform: scale(0.6);
            transform: scale(0.6); } }

@keyframes fly-coin {
  from {
    top: 50vh;
    left: 50vw;
    -webkit-transform: scale(1);
            transform: scale(1); }
  to {
    top: 0;
    left: 0;
    -webkit-transform: scale(0.6);
            transform: scale(0.6); } }

.page-index .video-wrap {
  padding: 50px 0; }

.virtual-list {
  height: 300px;
  overflow: hidden; }
  .virtual-list .page-list {
    height: 300px;
    overflow-y: auto;
    position: relative; }
    .virtual-list .page-list .hidden-list {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      z-index: -10000;
      height: 1px;
      overflow-y: auto;
      opacity: 0; }

